<template>
  <div class="example" ref="dom">
  </div>
</template>

<script>
import * as THREE from 'three';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
const WEBURL = 'https://threesource-1301234930.cos.ap-shanghai.myqcloud.com/'
export default {
  name: 'home',
  mounted(){
    const toast =  this.$toast.loading({
      duration: 0, //  toast
      forbidClick: true,
      message: '0%',
    });
    let camera, scene, renderer;
    let name = this.$route.query.name || 'box.gltf'
    console.log(name)
    camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 0.25, 20 );
    camera.position.set( - 1.8, 0.6, 500);

    scene = new THREE.Scene();
    renderer = new THREE.WebGLRenderer( { antialias: true,alpha: true } );
    renderer.setPixelRatio( window.devicePixelRatio );
    renderer.setSize( window.innerWidth, window.innerHeight );
    renderer.toneMapping = THREE.ACESFilmicToneMapping;
    renderer.toneMappingExposure = 1;
    renderer.outputEncoding = THREE.sRGBEncoding;
    this.$refs.dom.appendChild( renderer.domElement );

    const controls = new OrbitControls( camera, renderer.domElement );
    controls.addEventListener( 'change', render ); // use if there is no animation loop
    controls.minDistance = 2;
    controls.maxDistance = 10;
    controls.target.set( 0, 0, - 0.2 );
    controls.update();

    const loader = new GLTFLoader();
    loader.load( `${WEBURL}${name}`,  ( gltf )=> {
      console.log(gltf)
        gltf.scene.traverse( function ( child ) {
            if ( child.isMesh ) {
              child.material.emissive =  child.material.color;
              child.material.emissiveMap = child.material.map ;
            }
        } );
        scene.add( gltf.scene );
        render();
        this.$toast.clear()
    } ,(event)=>{ //progress
      let percent =parseInt((event.loaded / event.total)*100)
      toast.message = ` ${percent}%`
    },  ( error )=> {
      console.log(error)
      this.$toast.clear()
  } );
    render();
    function render() {
        renderer.render( scene, camera );
    }
  }
}
</script>
<style>
.example{
  width:100%;
  height:100%
}
</style>
